.section-phrase__container{
    display: flex;
    flex-direction: column;
    row-gap: 5rem;
}
    .section-phrase__container__ph{
        font-size: 22px ;
    }
    .section-phrase__container__cta{
        background-color: rgba(255, 255, 255, 0);
        border-radius: 43px;
        font-size: 2rem;
        border: 1px solid black;
        cursor: pointer;
        padding: 20px;
        width: fit-content;
        
    }
    .section-phrase__container__cta:hover{
        background-color: #C3DCC1;
    }

/*RESPONSIVE DESKTOP*/
@media (min-width:950px) {
    .section-phrase{
        justify-content: space-evenly;
    }
        .section-phrase__container__ph{
            font-size:var(--home-intro-size) ;
        }
        .section-phrase__container__cta{
            font-size: 2rem;
            /* padding: 15px 17px 15px 17px; */
        }
}