/* Looper styles */
@keyframes slideAnim {
    from {
      transform: translateX(0%);
    }
    to {
      transform: translateX(-100%);
    }
  }
  
  .looper {
    width: 100%;
    overflow: hidden;
    padding-top: 2rem;
  }
  
  .looper__innerList {
    display: flex;
    justify-content: center;
    width: fit-content;
  }
  
  .looper__innerList[data-animate="true"] .looper__listInstance {
    animation: slideAnim linear infinite;   
  }
  
  .looper__listInstance {
    display: flex;
    width: max-content;
  
    animation: none;
  }


  .contentBlock {
    display: flex;
    align-items: center;
    justify-content: center;
    /* margin: 10px; */
    padding: 16px; 
    font-weight: 600;
    text-align: center;
    border-radius: 16px;
  }
  /* @media (min-width:1400px){
    .contentBlock{
      width: 600px;
      height: 600px;
    }
  }  */
  @media (min-width:1300px){
    .contentBlock{
      width: 600px;
      height: 600px;
    }
  } 