.welcome-home{
    display: flex;
    flex: 1;
    flex-direction: column;
    /* align-items: center; */
    justify-content: center;
    /* padding-top: 2rem; */
    
}
    .welcome-home__subtitle{
        font-size: 2rem;
        font-weight: var(--homeMob-subtitle-font);
        /* font-family: var(--homeMob-subtitle-font); */
        margin-top: 0;
        margin-bottom: 0;
        align-self: start;
    }

    .welcome-home__subtitle:nth-last-child(1){
        align-self: flex-end;
    }
    .welcome-home__subtitle:nth-last-child(2){
        align-self: flex-end;
    }
    .welcome-home__subtitle:nth-last-child(3){
        align-self: flex-end;
    }
    .welcome-home__subtitle:nth-last-child(4){
        overflow: hidden;
        border-right: .1em solid var(--txt-cta);
        white-space: nowrap;
        letter-spacing: .15em;
        animation:typing 4s steps(20, end), blink-caret .75s step-end infinite; 
        

        /* animation-iteration-count: 2, 0,  infinite; */
    }
@media(min-width:700px){
    .welcome-home__subtitle{
        white-space: nowrap;
    }
}
    .welcome-home__subtitle{
        overflow: hidden;
        border-right: .1em solid var(--txt-cta);
        /* white-space: nowrap; */
        letter-spacing: .15em;
        animation:typing 4s steps(15, end), blink-caret .75s step-end infinite; 
        

        /* animation-iteration-count: 2, 0,  infinite; */
    }
    /* The typing effect */
    @keyframes typing {
        from { width: 0 }
        to { width: 50% }
    }
    
    /* The typewriter cursor effect */
    @keyframes blink-caret {
        from, to { border-color: transparent }
        50% { border-color: var(--txt-cta); }
    }
    


    .welcome-home__title{
        font-size:5em;
        font-family: "Bricolage Grotesque", sans-serif;
        font-weight: 500;
        margin-top:10px;
        margin-bottom: 10px;
    }

/*RESPONSIVE IPAD*/
@media (min-width:700px) {
    .welcome-home__subtitle{
        font-size: var(--home-subtitles-size) ;
    }
    .welcome-home__title{
        font-size: 6rem;
    }

}
/*RESPONSIVE DESKTOP*/
@media (min-width:950px) {
    /* .welcome-home{
        margin-left: 10rem;
        padding-top: 3rem;
    } */
    .welcome-home__subtitle{
        font-size: 1.8rem ;
    }
    .welcome-home__title{
        font-size: 7.5rem;
        max-width: 960px;
    }
}